@import url(https://cdn.jsdelivr.net/font-kopub/1.0/kopubdotum.css);
@import url(https://fonts.googleapis.com/earlyaccess/kopubbatang.css);
html, body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width:100%; height:100%; -webkit-overflow-scrolling: touch; -webkit-font-smoothing: antialiased; -webkit-text-size-adjust: none; -moz-text-size-adjust: none; -ms-text-size-adjust: none; }
html{overflow-y:scroll;}
body{font-family: 'Noto Sans KR', sans-serif; font-size: 14px; word-break:break-all;-ms-word-break:break-all; }
html, body, a, address, blockquote, br, fieldset, legend, label, form, input, select, textarea, button, table, th, td caption, cite, code, dl, dt, dd, h1, h2, h3, h4, h5, h6, p, div, iframe, ul, ol, li, hr, img{margin:0; padding:0;}
h1, h2, h3, h4, h5, h6 {font-size:14px; text-align:left;}
ul, ol, li {list-style:none;}
abbr,acronym {border:0;}
img{vertical-align:middle; border:none; max-width: 100%;}
img, fieldset, iframe{border:none;}
em, address{font-style:normal;}
textarea{overflow:auto; resize: none;}
table{border-collapse:collapse; border-spacing:0;}
input, select, textarea{vertical-align:middle;}
strong, em{font-weight: bold;}
a, a:link, a:visited{text-decoration:none;}
a:active, a:hover{text-decoration:none;}
/* input box reset */
input::-webkit-input-placeholder {font-size: 13px;}
input::-moz-placeholder {font-size: 13px;}
input:-ms-input-placeholder {font-size: 13px;}
input::-ms-input-placeholder {font-size: 13px;}
input::placeholder {font-size: 13px;}
input::-webkit-input-placeholder {font-size: 13px;}
input:-ms-input-placeholder {font-size: 13px;}
input[type=text]::-ms-clear {display:none;}
input[type=text]{-webkit-border-radius: 0px; -webkit-appearance: none;}
input[type=button]{-webkit-border-radius: 0px;}
/* input box color */
input:-webkit-autofill {-webkit-box-shadow: 0 0 0 30px #fff inset ; -webkit-text-fill-color: #000;} 
input:-webkit-autofill, input:-webkit-autofill:hover, input:-webkit-autofill:focus, 
input:-webkit-autofill:active {-webkit-transition: background-color 5000s ease-in-out 0s;transition: background-color 5000s ease-in-out 0s;}
/* select no round */
select{-webkit-appearance: none; -webkit-border-radius: 0px; -moz-appearance: none;background-color: #fff; background-position: right 50%; background-repeat: no-repeat; background-image: url(data:image/png;base64,);}
*{outline: none;}

