p{margin:20px}header .input_wrap li:nth-child(2){margin-left:0}header .input_wrap li:nth-child(4){width:100%}header .input_wrap li:nth-child(4) input{width:100%}.loading{width:100%}.font-list{margin-bottom:30px;padding:15px 10px;width:340px}}

